<template>
  <div class="demo">
    <div class="table-centered">
      <div class="table-wrap">
        <div
          class="codelos-main-header"
          :style="getTableHeadingStyle"
          v-if="isShowHeading"
        >
          <input
            type="text"
            name="heading"
            id="heading"
            :headingValue="tableHeading"
            v-model="tableHeading"
            @input="onTableHeadingChange"
            :disabled="!editable"
          />
        </div>
        <table id="table" class="table">
          <tr
            v-for="(tableRow, tableRowIndex) in tableContent"
            :key="`tableRowIndex-${tableRowIndex}`"
            :id="`tableRowIndex-${tableRowIndex}`"
            :class="{
              selected: selectedRow === tableRowIndex,
              'codelos-table-header': isHeaderRow(tableRowIndex),
            }"
            :draggable="editable"
            @dragstart="onStartRowDrag($event)"
            @dragover="onDragRow($event)"
            @dragend="onDragRowEnd()"
          >
            <td
              v-for="(tableRowItem, tableRowItemIndex) in tableRow"
              :key="`tableRowItemIndex-${tableRowItemIndex}`"
              @mouseover="
                setBodyCurrentItemHover(
                  $event,
                  tableRowIndex,
                  tableRowItemIndex
                )
              "
              @mouseleave="removeBodyCurrentItemHover()"
              :class="{
                selected: selectedCol === tableRowItemIndex,
                cellSelected:
                  selectedCell.row === tableRowIndex &&
                  selectedCell.col === tableRowItemIndex,
              }"
            >
              <div
                class="row-selector row-handle"
                v-if="
                  tableRowItemIndex === 0 && rowOnHoverIdx === tableRowIndex
                "
                @mouseover="removeRowHandleOnHover()"
                @mouseleave="addRowOrColHandleOnHover()"
                id="rowHandle"
              >
                <b-icon-grid3x3-gap-fill
                  @click="toggleRowModel($event, !rowModel.isVisible)"
                ></b-icon-grid3x3-gap-fill>
              </div>

              <div
                class="column-selector col-handle"
                v-show="
                  tableRowIndex === 0 && colOnHoverIdx === tableRowItemIndex
                "
                @mouseover="removeColHandleOnHover()"
                @mouseleave="addRowOrColHandleOnHover()"
                draggable="true"
                id="columnHandle"
              >
                <b-icon-grid3x3-gap-fill
                  @click="toggleColModel($event, !colModel.isVisible)"
                ></b-icon-grid3x3-gap-fill>
              </div>
              <input
                type="text"
                :id="`tableContent-${tableRowIndex}-${tableRowItemIndex}`"
                :cellValue="tableContent[tableRowIndex][tableRowItemIndex]"
                v-model="tableContent[tableRowIndex][tableRowItemIndex]"
                @focus="getSelectedCell(tableRowIndex, tableRowItemIndex)"
                @input="onCellInput"
                :disabled="!editable"
              />
              <!--  -->
              <div
                class="dropzone-line"
                :class="{ horizontal: isDropZoneInRow, top: isDropZoneRowTop }"
                :style="{
                  opacity:
                    showDropZoneAt === tableRowIndex ? dropZoneOpacity : 0,
                }"
              ></div>
            </td>
          </tr>
        </table>

        <div class="add-new-col" v-if="editable">
          <b-button color="#b5b5b5" @click="addNewCol"
            ><b-icon-plus></b-icon-plus
          ></b-button>
        </div>
        <div class="add-new-row" v-if="editable">
          <b-button @click="addNewRow" color="#b5b5b5"
            ><b-icon-plus></b-icon-plus
          ></b-button>
        </div>
      </div>
    </div>
    <div v-show="rowModel.isVisible">
      <div id="row-overly" class="modal-backdrop custom-backdrop"></div>
      <div
        class="row-model"
        :style="{
          transform: getRowModelOpenPlace,
        }"
      >
        <div class="close-btn">
          <b-icon-x-lg @click="toggleRowModel($event, false)"></b-icon-x-lg>
        </div>
        <div class="row-model-content">
          <ul>
            <li v-if="rowOnHoverIdx === 0">
              <span @click="toggleHeadingInTable($event)">{{
                isShowHeading ? 'Überschrift entfernen' : 'Überschrift anzeigen'
              }}</span>
            </li>
            <li v-if="rowOnHoverIdx === 0">
              <span @click="toggleRowHeader($event)">Kopfzeile</span>
            </li>
            <li>
              <span @click="deleteRow">löschen</span>
            </li>
            <li>
              <span @click="insertRow($event, 'above')">Oben einfügen</span>
            </li>
            <li>
              <span @click="insertRow($event, 'below')">Unten einfügen</span>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div v-show="colModel.isVisible">
      <div id="col-overly" class="modal-backdrop custom-backdrop"></div>

      <div
        class="row-model"
        :style="{
          transform: getColModelOpenPlace,
        }"
      >
        <div class="close-btn">
          <b-icon-x-lg @click="toggleColModel($event, false)"></b-icon-x-lg>
        </div>
        <div class="row-model-content">
          <ul>
            <li>
              <span @click="deleteCol">löschen</span>
            </li>
            <li>
              <span @click="insertCol($event, 'left')">Links einfügen</span>
            </li>
            <li>
              <span @click="insertCol($event, 'right')">Rechts einfügen</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- object from table builder : {{ tableContent }} -->
  </div>
</template>

<script>
export default {
  name: 'DynamicTale',
  data() {
    return {
      tableContent: [],
      tableLayout: [],
      tableContentBody: null,
      tableHTML: '',
      rowOnHoverIdx: 0,
      selectedRow: null,
      selectedCol: null,
      colOnHoverIdx: 0,
      isHoverOnHandle: false,
      rowModel: {
        isVisible: false,
        posX: 0,
        posY: 0,
      },
      colModel: {
        isVisible: false,
        posX: 0,
        posY: 0,
      },
      selectedCell: {
        row: null,
        col: null,
      },
      rowIndex: {
        old: null,
        new: null,
      },
      table: null,
      draggingEle: null,
      draggingColumnIndex: null,
      placeholder: null,
      list: null,
      isDraggingStarted: false,
      x: 0,
      y: 0,
      cloneListTable: null,
      colDrag: false,
      dropZoneOpacity: 0,
      showDropZoneAt: null,
      isDropZoneInRow: false,
      isDropZoneRowTop: false,
      isColDrag: false,
      visualizeDropZoneAtRight: false,
      visualizeDropZoneAtLeft: false,
      tableHeading: null,
    };
  },

  props: {
    tableSchema: {
      type: Object,
      default: () => ({
        rows: 3,
        cols: 2,
        content: null,
        layout: null,
        heading: null,
      }),
    },
    hasHeader: { type: Boolean, default: false },
    isShowHeading: { type: Boolean, default: false },
    userTheme: { type: Object, default: null },
    editable: { type: Boolean, default: true },
  },

  mounted() {
    document
      .getElementById('row-overly')
      .addEventListener('click', () => this.onOverlayClick());
    document
      .getElementById('col-overly')
      .addEventListener('click', () => this.onOverlayClick());
    if (!this.editable) {
      this.rowOnHoverIdx = null;
      this.colOnHoverIdx = null;
    }
  },

  created() {
    this.setInitialTableContent();
    setTimeout(() => {
      // Attach the Drag Start listeners to `document` When Component Crated
      this.addDargStartOnCol();
    }, 1000);
  },
  updated() {
    setTimeout(() => {
      // Attach the Drag Start listeners to `document` When Component Updated
      this.addDargStartOnCol();
    }, 1000);
  },

  methods: {
    addDargStartOnCol() {
      if (!this.editable) return;
      const table = document.getElementById('table');
      if (!table) return;
      this.table = table;
      const colSelector = Array.from(
        this.table.getElementsByClassName('column-selector')
      );
      if (!colSelector) return;
      colSelector.forEach((headerCell, cellIndex) => {
        headerCell.addEventListener('dragstart', (event) => {
          this.onColDragStart(event, cellIndex);
        });
      });
    },

    //Crate Table on Component Load
    setInitialTableContent() {
      if (this.tableSchema.heading)
        this.tableHeading = this.tableSchema.heading;

      if (this.tableSchema.content) {
        this.tableContent = this.tableSchema.content;
      } else {
        this.tableContent = this.tableSchema.rows
          ? new Array(this.tableSchema.rows).fill([])
          : [];
        this.tableContent = this.tableContent.length
          ? this.tableContent.map(() =>
              new Array(this.tableSchema.cols || 0).fill('')
            )
          : [];

        this.$emit('tableContentChange', this.clone(this.tableContent));
      }

      if (this.tableSchema.layout) {
        this.tableLayout = this.tableSchema.layout;
      } else {
        this.tableLayout = this.getRowsCountOfTable
          ? new Array(this.getRowsCountOfTable).fill([])
          : new Array(this.tableSchema.rows).fill([]);

        this.tableLayout = this.tableLayout.length
          ? this.tableLayout.map(() =>
              new Array(this.getColsCountOfTable).fill({})
            )
          : [];
      }
    },

    getHeaderRow(row, rowIdx) {
      if (this.hasHeader && rowIdx == 0) {
        return row;
      } else return [];
    },

    getTableRows(row, rowIdx) {
      if (this.hasHeader && rowIdx == 0) return [];
      else return row;
    },

    toggleRowModel(event, val) {
      val ? (this.selectedRow = this.rowOnHoverIdx) : (this.selectedRow = null);
      this.selectedCol = null;

      this.rowModel.posX = event.target.getBoundingClientRect().x + 8;
      this.rowModel.posY = event.target.getBoundingClientRect().y + 8;
      this.removeSelectedCell();
      this.rowModel.isVisible = val;
      this.removeBodyCurrentItemHover(val);
    },

    toggleColModel(event, val) {
      val ? (this.selectedCol = this.colOnHoverIdx) : (this.selectedCol = null);
      this.selectedRow = null;
      this.colModel.posX = event.target.getBoundingClientRect().x + 8;
      this.colModel.posY = event.target.getBoundingClientRect().y + 8;
      this.removeSelectedCell();
      this.colModel.isVisible = val;
    },

    setBodyCurrentItemHover(e, rowIndex, colIndex) {
      if (!this.editable) return;
      if (this.isHoverOnHandle) {
        return;
      }
      if (!this.colModel.isVisible && !this.rowModel.isVisible) {
        this.rowOnHoverIdx = rowIndex;
        this.colOnHoverIdx = colIndex;
      }
    },

    removeBodyCurrentItemHover() {
      if (this.rowModel.isVisible || this.colModel.isVisible) {
        return;
      }
      this.rowOnHoverIdx = null;
      this.colOnHoverIdx = null;
    },

    addNewRow() {
      this.tableContent.push(new Array(this.getColsCountOfTable).fill(''));
      this.tableLayout.push(new Array(this.getColsCountOfTable).fill({}));
      this.$emit('rowCountChange', this.getRowsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    addNewCol() {
      this.tableContent.forEach((el) => el.push(''));
      this.tableLayout.forEach((el) => el.push({}));
      this.$emit('colCountChange', this.getColsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    getHTMLStringNew() {
      let htmlString = `<table> <thead> <tr>`;

      this.tableContent.header.forEach(
        (el) => (htmlString += `<th> <input type='text' value='${el}'/>  </th>`)
      );

      htmlString += `</tr> </thead>`;

      htmlString += `<tbody>`;

      this.tableContent.body.forEach((row) => {
        let tmp = '<tr>';
        row.forEach(
          (cell) => (tmp += `<td> <input type='text' value='${cell}'/>  </td>`)
        );
        tmp += '</tr>';
        htmlString += tmp;
      });
      htmlString += `</table>`;

      this.tableHTML = htmlString;
    },

    deleteRow(event) {
      this.tableContent.splice(this.rowOnHoverIdx, 1);
      this.tableLayout.splice(this.rowOnHoverIdx, 1);
      this.toggleRowModel(event, false);
      this.$emit('rowCountChange', this.getRowsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    deleteCol(event) {
      this.tableContent.forEach((singleRow) =>
        singleRow.splice(this.colOnHoverIdx, 1)
      );
      this.tableLayout.forEach((singleRow) =>
        singleRow.splice(this.colOnHoverIdx, 1)
      );

      this.toggleColModel(event, false);
      this.$emit('colCountChange', this.getColsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    insertCol(event, val) {
      this.tableContent.forEach((el) => {
        val === 'right'
          ? el.splice(this.colOnHoverIdx + 1, 0, '')
          : el.splice(this.colOnHoverIdx, 0, '');
      });
      this.tableLayout.forEach((el) => {
        val === 'right'
          ? el.splice(this.colOnHoverIdx + 1, 0, {})
          : el.splice(this.colOnHoverIdx, 0, {});
      });
      this.removeSelectedCell();
      this.toggleColModel(event, false);

      this.$emit('colCountChange', this.getColsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },
    insertRow(event, val) {
      val === 'below'
        ? this.tableContent.splice(
            this.rowOnHoverIdx + 1,
            0,
            new Array(this.getColsCountOfTable).fill('')
          )
        : this.tableContent.splice(
            this.rowOnHoverIdx,
            0,
            new Array(this.getColsCountOfTable).fill('')
          );

      val === 'below'
        ? this.tableLayout.splice(
            this.rowOnHoverIdx + 1,
            0,
            new Array(this.getColsCountOfTable).fill({})
          )
        : this.tableLayout.splice(
            this.rowOnHoverIdx,
            0,
            new Array(this.getColsCountOfTable).fill({})
          );
      this.removeSelectedCell();

      this.toggleRowModel(event, false);

      this.$emit('rowCountChange', this.getRowsCountOfTable);
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    getSelectedCell() {
      this.selectedCell.row = this.rowOnHoverIdx;
      this.selectedCell.col = this.colOnHoverIdx;
      this.selectedCol = null;
      this.selectedRow = null;
    },

    removeSelectedCell() {
      this.selectedCell.row = null;
      this.selectedCell.col = null;
    },

    onOverlayClick() {
      this.colModel.isVisible = false;
      this.rowModel.isVisible = false;
    },

    removeRowHandleOnHover() {
      this.isHoverOnHandle = true;
      this.colOnHoverIdx = null;
    },

    removeColHandleOnHover() {
      this.isHoverOnHandle = true;
      this.rowOnHoverIdx = null;
    },

    addRowOrColHandleOnHover() {
      this.isHoverOnHandle = false;
    },

    toggleRowHeader(event) {
      this.$emit('toggleRowHeader');
      this.toggleRowModel(event, false);
    },

    isHeaderRow(index = 0) {
      return index === 0 && this.hasHeader;
    },
    onStartRowDrag(e) {
      this.selectedCol = null;
      this.selectedCell.row = null;
      this.selectedCell.col = null;
      const colHandle = document.getElementById('columnHandle');
      if (colHandle.id === e.target.id) {
        return;
      }
      this.shadow = e.target;
      this.selectedRow = this.rowOnHoverIdx;
    },

    onDragRow(e) {
      if (this.isColDrag) {
        return;
      }
      this.selectedRow = null;
      let children = Array.from(
        e.target.parentNode.parentNode.parentNode.children
      );
      if (
        children.indexOf(e.target.parentNode.parentNode) !==
          children.indexOf(this.shadow) &&
        children.indexOf(this.shadow) >= 0
      ) {
        this.rowIndex.old = children.indexOf(this.shadow);
        this.rowIndex.new = children.indexOf(e.target.parentNode.parentNode);
        this.showDropZoneAt = this.rowIndex.new;
        this.isDropZoneInRow = true;
        this.dropZoneOpacity = 1;
        this.rowIndex.old > this.rowIndex.new
          ? (this.isDropZoneRowTop = true)
          : (this.isDropZoneRowTop = false);
      }
    },

    onDragRowEnd() {
      [
        this.tableContent[this.rowIndex.old],
        this.tableContent[this.rowIndex.new],
      ] = [
        this.tableContent[this.rowIndex.new],
        this.tableContent[this.rowIndex.old],
      ];
      this.selectedRow = this.rowIndex.new;
      this.rowOnHoverIdx = this.rowIndex.new;
      this.rowIndex.old = null;
      this.rowIndex.new = null;
      this.dropZoneOpacity = 0;
      this.isHoverOnHandle = false;
      this.isDropZoneInRow = false;
      this.isDropZoneRowTop = false;
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },

    // Swap two nodes
    swap(nodeA, nodeB) {
      if (nodeA === null || nodeB === null) return;
      const parentA = nodeA.parentNode;
      const siblingA = nodeA.nextSibling === nodeB ? nodeA : nodeA.nextSibling;

      // Move `nodeA` to before the `nodeB`
      nodeB.parentNode.insertBefore(nodeA, nodeB);

      // Move `nodeB` to before the sibling of `nodeA`
      parentA.insertBefore(nodeB, siblingA);
    },

    // Check if `nodeA` is on the left of `nodeB`
    isOnLeft(nodeA, nodeB) {
      // Get the bounding rectangle of nodes
      const rectA = nodeA.getBoundingClientRect();
      const rectB = nodeB.getBoundingClientRect();

      return rectA.left + rectA.width / 2 < rectB.left + rectB.width / 2;
    },

    cloneTable() {
      this.list = document.createElement('div');
      this.list.classList.add('clone-list');
      this.list.style.position = 'absolute';

      this.table.parentNode.insertBefore(this.list, this.table);

      // Hide the original table
      this.table.style.visibility = 'hidden';

      // Get all cells
      const originalCells = [].slice.call(this.table.querySelectorAll('td'));

      // Loop through the header cells
      const originalHeaderCells = Array.from(
        [].slice.call(this.table.querySelectorAll('tr'))[0].children
      );
      const numColumns = originalHeaderCells.length;

      originalHeaderCells.forEach((headerCell, headerIndex) => {
        const width = parseInt(window.getComputedStyle(headerCell).width);

        // Create a new table from given row
        const item = document.createElement('div');
        item.classList.add('draggable');
        item.setAttribute('id', `colElementIdx-${headerIndex + 1}`);
        const newTable = document.createElement('table');
        newTable.setAttribute('class', 'clone-table');
        newTable.style.width = `${width}px`;

        let newRow = document.createElement('tr');

        const cells = originalCells.filter((c, idx) => {
          return (idx - headerIndex) % numColumns === 0;
        });
        cells.forEach((cell) => {
          const newCell = cell.cloneNode(true);
          newCell.style.width = `${width}px`;
          newRow = document.createElement('tr');
          newRow.appendChild(newCell);
          newTable.appendChild(newRow);
        });

        item.appendChild(newTable);
        this.list.appendChild(item);
      });
    },

    onColDragStart(e) {
      this.isColDrag = true;
      this.selectedCol = null;
      this.selectedCell.row = null;
      this.selectedCell.col = null;

      let children = Array.from(e.target.parentNode.parentNode.children);
      this.draggingColumnIndex = children.indexOf(e.target.parentNode);

      this.x = e.clientX - e.target.parentNode.offsetLeft;
      this.y = e.clientY - e.target.parentNode.offsetTop;

      document.addEventListener('dragover', this.onColDrag);

      document.addEventListener('dragend', (event) => {
        this.onColDragEnd(event);
      });
    },

    onColDrag(e) {
      if (!this.isDraggingStarted) {
        this.isDraggingStarted = true;

        this.cloneTable();

        this.draggingEle = [].slice.call(this.list.children)[
          this.draggingColumnIndex
        ];
        this.draggingEle.classList.add('dragging');

        const originalHeaderCells = Array.from(
          [].slice.call(this.table.querySelectorAll('tr'))
        );
        // Let the placeholder take the height of dragging element
        // So the next element won't move to the left or right
        // to fill the dragging element space
        this.placeholder = document.createElement('div');
        this.placeholder.classList.add('placeholder');
        const newTable = document.createElement('table');
        newTable.style.width = `${this.draggingEle.offsetWidth}px`;
        newTable.classList.add('placeholder-table');

        originalHeaderCells.forEach(() => {
          const newRow = document.createElement('tr');
          newRow.insertCell();
          newTable.appendChild(newRow);
        });
        this.placeholder.appendChild(newTable);
        this.draggingEle.parentNode.insertBefore(
          this.placeholder,
          this.draggingEle.nextSibling
        );
        this.placeholder.style.width = `${this.draggingEle.offsetWidth}px`;
      }

      // Set position for dragging element
      this.draggingEle.style.position = 'absolute';
      this.draggingEle.style.top = `${
        this.draggingEle.offsetTop + e.clientY - this.y
      }px`;
      this.draggingEle.style.left = `${
        this.draggingEle.offsetLeft + e.clientX - this.x
      }px`;

      // Reassign the position of mouse
      this.x = e.clientX;
      this.y = e.clientY;

      // The current order
      // prevEle
      // draggingEle
      // placeholder
      // nextEle
      const prevEle = this.draggingEle.previousElementSibling;
      const nextEle = this.placeholder.nextElementSibling;

      let nodeA;
      let nodeB;
      let colDraggingIdx;
      const cloneListTable = Array.from(this.list.children);

      if (nextEle !== null) {
        nodeA = nextEle.getBoundingClientRect();
      }
      if (prevEle !== null) {
        nodeB = prevEle.getBoundingClientRect();
      }

      // // The dragging element is above the previous element
      // // User moves the dragging element to the left
      if (prevEle && this.isOnLeft(this.draggingEle, prevEle)) {
        cloneListTable.forEach((e, i) => {
          if (e.classList.contains('dragging')) {
            colDraggingIdx = i;
          }
        });
        if (this.list.children[colDraggingIdx - 1].children.length !== 0) {
          const leftElementFromDraggingEle = Array.from(
            this.list.children[colDraggingIdx - 1].children[0].children
          );
          leftElementFromDraggingEle.forEach((element) =>
            Array.from(element.children).forEach((subElement) => {
              this.removeVisualizeDropZone(subElement, 'verticle', 'left', 0);
            })
          );
        }

        // The current order    -> The new order
        // prevEle              -> placeholder
        // draggingEle          -> draggingEle
        // placeholder          -> prevEle
        this.swap(this.placeholder, this.draggingEle);
        this.swap(this.placeholder, prevEle);
      }

      // The dragging element is below the next element
      // User moves the dragging element to the bottom
      if (nextEle && this.isOnLeft(nextEle, this.draggingEle)) {
        cloneListTable.forEach((e, i) => {
          if (e.classList.contains('placeholder')) {
            colDraggingIdx = i;
          }
        });
        if (this.list.children[colDraggingIdx + 1] !== undefined) {
          if (this.list.children[colDraggingIdx + 1].children.length !== 0) {
            const leftElementFromDraggingEle = Array.from(
              this.list.children[colDraggingIdx + 1].children[0].children
            );
            leftElementFromDraggingEle.forEach((element) =>
              Array.from(element.children).forEach((subElement) => {
                this.removeVisualizeDropZone(
                  subElement,
                  'verticle',
                  'right',
                  0
                );
              })
            );
          }
        }
        // The current order    -> The new order
        // draggingEle          -> nextEle
        // placeholder          -> placeholder
        // nextEle              -> draggingEle
        this.swap(nextEle, this.placeholder);
        this.swap(nextEle, this.draggingEle);
      }

      if (nodeA) {
        if (nodeA.left < e.clientX) {
          this.visualizeDropZoneAtRight = true;
          this.visualizeDropZoneAtLeft = false;
          cloneListTable.forEach((e, i) => {
            if (e.classList.contains('dragging')) {
              colDraggingIdx = i;
            }
          });
          if (this.list.children[colDraggingIdx - 1] !== undefined) {
            if (this.list.children[colDraggingIdx - 1].children.length !== 0) {
              const leftElementFromDraggingEle = Array.from(
                this.list.children[colDraggingIdx - 1].children[0].children
              );
              leftElementFromDraggingEle.forEach((element) =>
                Array.from(element.children).forEach((subElement) => {
                  this.removeVisualizeDropZone(
                    subElement,
                    'verticle',
                    'left',
                    0
                  );
                })
              );
            }
          }
          cloneListTable.forEach((e, i) => {
            if (e.classList.contains('placeholder')) {
              colDraggingIdx = i;
            }
          });

          if (
            !this.list.children[colDraggingIdx + 1].classList.contains(
              'placeholder'
            )
          ) {
            const leftElementFromDraggingEle = Array.from(
              this.list.children[colDraggingIdx + 1].children[0].children
            );
            leftElementFromDraggingEle.forEach((element) =>
              Array.from(element.children).forEach((subElement) => {
                this.addVisualizeDropZone(subElement, 'verticle', 'right', 1);
              })
            );
          }
        }
      }

      if (nodeB) {
        if (nodeB.left + nodeB.width > e.clientX) {
          this.visualizeDropZoneAtLeft = true;
          this.visualizeDropZoneAtRight = false;
          cloneListTable.forEach((e, i) => {
            if (e.classList.contains('placeholder')) {
              colDraggingIdx = i;
            }
          });
          if (this.list.children[colDraggingIdx + 1] !== undefined) {
            if (
              !this.list.children[colDraggingIdx + 1].classList.contains(
                'placeholder'
              )
            ) {
              const leftElementFromDraggingEle = Array.from(
                this.list.children[colDraggingIdx + 1].children[0].children
              );

              leftElementFromDraggingEle.forEach((element) =>
                Array.from(element.children).forEach((subElement) => {
                  this.removeVisualizeDropZone(
                    subElement,
                    'verticle',
                    'right',
                    0
                  );
                })
              );
            }
          }

          cloneListTable.forEach((e, i) => {
            if (e.classList.contains('dragging')) {
              colDraggingIdx = i;
            }
          });

          if (this.list.children[colDraggingIdx + 1] !== undefined) {
            if (
              !this.list.children[colDraggingIdx + 1].classList.contains(
                'dragging'
              ) &&
              !this.list.children[colDraggingIdx + 1].classList.contains(
                'draggable'
              )
            ) {
              const leftElementFromDraggingEle = Array.from(
                this.list.children[colDraggingIdx - 1].children[0].children
              );
              leftElementFromDraggingEle.forEach((element) =>
                Array.from(element.children).forEach((subElement) => {
                  this.addVisualizeDropZone(subElement, 'verticle', 'top', 1);
                })
              );
            }
          }
        }
      }
    },

    onColDragEnd() {
      this.isColDrag = false;

      if (this.placeholder.parentNode === null) return;
      if (this.placeholder.nextElementSiblings !== null) {
        if (this.visualizeDropZoneAtRight) {
          this.swap(this.placeholder.nextElementSibling, this.placeholder);
          this.swap(this.placeholder.previousElementSibling, this.draggingEle);
        }
      }

      if (this.visualizeDropZoneAtLeft) {
        this.swap(this.draggingEle, this.draggingEle.previousElementSibling);
        this.swap(this.placeholder, this.placeholder.previousElementSibling);
      }
      // // Remove the placeholder
      this.placeholder &&
        this.placeholder.parentNode.removeChild(this.placeholder);

      this.draggingEle.classList.remove('dragging');
      this.draggingEle.style.removeProperty('top');
      this.draggingEle.style.removeProperty('left');
      this.draggingEle.style.removeProperty('position');

      // Get the end index
      const endColumnIndex = [].slice
        .call(this.list.children)
        .indexOf(this.draggingEle);

      this.isDraggingStarted = false;

      // Remove the `list` element
      this.list.parentNode.removeChild(this.list);

      this.tableContent.map((row) => {
        const element = row.splice(this.draggingColumnIndex, 1)[0];
        row.splice(endColumnIndex, 0, element);
      });

      this.$emit('tableContentChange', this.clone(this.tableContent));

      // Bring back the table
      this.table.style.removeProperty('visibility');

      // Remove the handlers of `mousemove` and `mouseup`
      document.removeEventListener('dragover', this.onColDrag);
      document.removeEventListener('dragend', this.onColDragEnd);
      this.colOnHoverIdx = endColumnIndex;
      this.selectedCol = endColumnIndex;
      this.selectedRow = null;
    },

    addVisualizeDropZone(subElement, align, side, opacity) {
      Array.from(subElement.children)[2].classList.add(align);
      Array.from(subElement.children)[2].classList.add(side);
      Array.from(subElement.children)[2].style.opacity = opacity;
    },

    removeVisualizeDropZone(subElement, align, side, opacity) {
      Array.from(subElement.children)[2].classList.remove(align);
      Array.from(subElement.children)[2].classList.remove(side);
      Array.from(subElement.children)[2].style.opacity = opacity;
    },

    clone(obj) {
      return JSON.parse(JSON.stringify(obj));
    },

    onCellInput() {
      this.$emit('tableContentChange', this.clone(this.tableContent));
    },
    toggleHeadingInTable(event) {
      this.tableHeading = null;
      this.toggleRowModel(event, false);
      this.$emit('toggleHeadingInTable');
    },
    onTableHeadingChange() {
      this.$emit('onTableHeadingChange', this.tableHeading);
    },
  },

  computed: {
    getRowModelOpenPlace() {
      return `translate( ${this.rowModel.posX}px , ${this.rowModel.posY}px )`;
    },

    getColModelOpenPlace() {
      return `translate( ${this.colModel.posX}px , ${this.colModel.posY}px )`;
    },
    getColsCountOfTable() {
      return this.tableContent[0] ? this.tableContent[0].length : 0;
    },
    getRowsCountOfTable() {
      return this.tableContent.length || 0;
    },
    getTableHeadingStyle() {
      return {
        background:
          this.userTheme && this.userTheme.secondaryBrandColor
            ? this.userTheme.secondaryBrandColor
            : null,
        color:
          this.userTheme && this.userTheme.textColor
            ? this.userTheme.textColor
            : null,
      };
    },
  },
};
</script>

